.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 1000;
  pointer-events: none;
}

.loading {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 16px;
  padding: 20px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

  img {
    width: 60px;
    height: 60px;
  }

  p {
    text-align: center;
    opacity: 1;
    font-size: larger;
    margin: 0;
  }
}

.oversize-warning {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 10px;
  border: 1px solid #3f51b5;
  border-radius: 5px;
  background-color: #f5f5f5;
  color: #333;


  &.mixer {
    font-size: var(--form-field-entry-size);

    button {
      font-size: var(--form-field-entry-size);
      height: var(--scalable-text-heading-size);
    }
  }

  &.library {
    scale: .6;
    border: none;
  }

}


.highlight-row,
.highlight-col {
  position: absolute;
  background-color: transparent;
  border: 1px solid #3f51b5;
  opacity: 0.5;
  display: none;
  z-index: 1000;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
  pointer-events: none;
}

.highlight-row {
  width: 100%;
  height: var(--cell-width);
  left: 0px;
  top: 0px;
}

.highlight-col {
  height: 100%;
  width: var(--cell-width);
  left: 0px;
  top: 0px;
}

.origin-marker {
  position: absolute;
  color: black;
  width: var(--cell-width);
  height: var(--cell-width);
  left: 0px;
  top: calc(-1 * var(--cell-width));
  text-align: center;
}

.app-selection {
  display: block;
  position: absolute;
}

.draft-top {
  display: flex;
  justify-content: flex-end;


  &.oversize {
    display: none;
  }

  &.viewer {
    display: none;
  }

  &.library {
    padding-bottom: var(--cell-width);
  }
}




.treadling-container.viewer,
.weft-systems-container.viewer,
.weft-systems-canvas.viewer,
.weft-systems-container.viewer,
.weft-materials-canvas.viewer {
  display: none;
}



.draft-top.origin0 {
  flex-direction: column;
  align-items: flex-start;
}


.draft-top.origin3 {
  flex-direction: column;
  align-items: flex-end;
}

.draft-top.origin1 {
  flex-direction: column-reverse;
  align-items: flex-start;
}

.draft-top.origin2 {
